	module.exports = {"data":{"site":{"siteMetadata":{"title":"momorandum","subtitle":"momo's memorandums","copyright":"© All rights reserved.","author":{"name":"momo","twitter":"sbg"},"disqusShortname":"","url":"https://m.bug.org"}},"markdownRemark":{"id":"/Users/momo/web/gatsby-momo/src/pages/articles/2012-04-12-apache241/index.md absPath of file >>> MarkdownRemark","html":"<p>Apache 2.4.1 をインストールしてみる．</p>\n<p>2.4 系では，APR, APR-Util は添付されなくなったようだ．\n自分で取得してインストールすることにする．\n一緒にインストールする手段もあるけど，別に入れておいてもいいのでそちらの方法で．</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code class=\"language-shell\">cd work/apr-1.4.6\n./configure --prefix=/var/service/local\nmake\nmake test\nmake install\ncd ../apr-util-1.4.1\n./configure --prefix=/var/service/local\nmake\nmake test\nmake install</code></pre>\n      </div>\n<p>httpd をインストール．\nconfigure の段階で足りないライブラリは足しておくこと．</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-shell\"><code class=\"language-shell\">./configure --prefix=/var/service/local/apache24 \\\n    --with-apr=/var/service/local \\\n    --with-apr-util=/var/service/local \\\n    --enable-mods-shared=&quot;reallyall&quot;\nmake\nmake install</code></pre>\n      </div>\n<p>特に問題なくインストール完了．</p>","fields":{"tagSlugs":["/tags/apache/"]},"frontmatter":{"title":"Apache 2.4.1 install","tags":["Apache"],"date":"2012-04-12T00:00:00.000Z","description":"Apache 2.4.1 をインストールしてみる．"}}},"pathContext":{"slug":"/posts/2012041200/"}}

